<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Upload File
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <div v-show="visibleProgress" class="mb-6">
            <div class="d-flex justify-space-between">
              <div>{{ statuUpload }}</div>
              <div>{{ status===false?'':uploadPercentage+'%' }}</div>
            </div>
            <v-progress-linear               
              :value="uploadPercentage"
              :color="status===false?'error':'primary'"
            ></v-progress-linear>          
          </div>

          <v-text-field
            v-model="dataUpload.nama"
            label="Nama file"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-file-input
            outlined
            dense
            prepend-icon="mdi-file"
            show-size
            counter
            v-model="dataUpload.file"
            :rules="requiredRule"
            label="File upload"
          ></v-file-input>
          <v-file-input
            accept="image/*"
            outlined
            dense
            prepend-icon="mdi-image"
            show-size
            counter
            v-model="dataUpload.gambar"
            :rules="requiredRule"
            label="File gambar"
          ></v-file-input>
          <v-select
            v-for="(item, i) in dataUpload.acl" :key="i"
            v-model="dataUpload.acl[i]"
            :items="items"
            label="Pilihan akses"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-btn
            depressed
            color="success"
            small block
            class="mb-6"
            @click="dataUpload.acl.push('')">
            tambah akses
          </v-btn>

          <Notif :status="status" :msg="msg" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import axios from 'axios'
  import Notif from '@/components/Notif'
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataUpload: {
          nama: '',
          acl: [''],
          gambar: null,
          file: null
        },
        items: ['Unit', 'Admin'],
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil ditambahkan',
          error: 'Data gagal ditambahkan',
          visible: false
        },
        uploadPercentage: 0,
        visibleProgress: false,
        isLoading: false,
        status: null
      }
    },

    components: { Notif },

    computed: {
      statuUpload() {
        let status = ''
        if (this.isLoading === true) {
          status = 'Sedang mengupload'
        } else if (this.status === true) {
          status = 'Upload berhasil'
        } else if (this.status === false) {
          status = 'Upload gagal'
        }

        return status
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.visibleProgress = true
          this.isLoading = true
          const data = new FormData()
          data.append('nama', this.dataUpload.nama)
          this.dataUpload.acl.map(x => data.append('acl[]', x))          
          data.append('file', this.dataUpload.file)
          data.append('gambar', this.dataUpload.gambar)
          axios.post('https://bcs.edubrain.id/api/v1/admin/fileupload', data, {            
            headers: {
              'Authorization': 'bearer '+window.localStorage.getItem('token_admin'),
            },
            onUploadProgress: progressEvent => {
              this.uploadPercentage = Math.round( (progressEvent.loaded * 100) / progressEvent.total )
            },
          })
            .then(res => {
              if (res.status === 201) {
                this.$store.dispatch('dataFileupload/getDataFileupload')
                this.isLoading = false
                this.status = true
                this.msg.visible = true
                setTimeout(() => {
                  this.uploadPercentage = 0
                  this.visibleProgress = false
                  this.dialog = false
                }, 2000)
              }
            })
            .catch(err => {
              this.isLoading = false
              this.status = false
            })
        }
      },
      visible(val) {
        this.msg.visible = val
        this.status = null
      }
    }
  }
</script>
