<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataFileupload"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataFileupload.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<v-btn icon color="primary">
					<v-icon small>mdi-pencil</v-icon>
				</v-btn>
				<v-btn icon color="red">
					<v-icon small>mdi-trash-can</v-icon>
				</v-btn>
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama File', value: 'nama', sortable: false },
				{ text: 'level Akses', value: 'access', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			]
		}),

		components: {
			Loader
		},

		created() {
			this.$store.dispatch('dataFileupload/getDataFileupload')
			this.$store.dispatch('dataFileupload/updateIsLoading', true)
		},

		computed: {
			...mapState('dataFileupload', ['dataFileupload', 'isLoading'])
		}
	}
</script>